import { Header } from "@/components/common";
import { mapState } from "vuex";
export default {
  name: "Download",
  components: {
    Header
  },
  computed: {
    ...mapState(["local"]),
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
    locale() {
      return this.local.locale;
    }
  },
  mounted() {
    this.isBitKeep && BitKeepInvoke.setTitle(locale=='zh'?"下载 bitKeep":'Download bitKeep');
  },
  methods: {
    back() {
      this.$router.back();
    }
  }
};
